import { createRouter, createWebHashHistory} from 'vue-router'

const router = createRouter({
  // 修改
  // history: createWebHistory(import.meta.env.BASE_URL),
  history: createWebHashHistory(),

  routes: [
    {
      path: '/',
      redirect: { name: 'home' }
    },
    {
      path: '/admin/home',
      name: 'home',
      // route level code-splitting
      // this generates a separate chunk (About.[hash].js) for this route
      // which is lazy-loaded when the route is visited.
      component: () => import('../views/HomeView.vue')
    },
    {
      path: '/admin/modeDefinition',
      name: 'modeDefinition',
      component: () => import('../views/ModeDefinition.vue')
    },
    {
      path: '/admin/formDesigner',
      name: 'formDesigner',
      component: () => import('../views/FormDesigner.vue')
    },
    {
      path: '/admin/formFinish',
      name: 'formFinish',
      component: () => import('../views/FormFinish.vue')
    },
    {
      path: '/admin/formCollection',
      name: 'formCollection',
      component: () => import('../views/FormCollection.vue')
    },
    {
      path: '/admin/dataCollection',
      name: 'dataCollection',
      component: () => import('../views/DataCollection.vue')
    },
    {
      path: '/user/submission/',
      name: 'publicSubmission',
      component: () => import('../views/PublicSubmission.vue')
    },
    {
      path: '/user/submission/finish',
      name: 'submissionFinish',
      component: () => import('../views/SubmissionFinish.vue')
    },
  ]
})

export default router